.rmc-demo-wrapper {
	width: 288px;
	border-style: solid;
	border-width: 1px;
	border-color: #ebebeb;
	border-radius: 4px;
}

.rmc-demo-typography {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 14px;
}

.rmc-demo-content {
	padding: 8px;
}

.rmc-demo-placeholder {
	border-radius: 4px 4px 0px 0px;
	width: 288px;
	height: 188px;
	background-color: #1955e0;
	background-image: linear-gradient(
		43deg,
		#1955e0 0%,
		#4193ff 46%,
		#ff70f2 100%
	);
}

.rmc-demo-row-button {
	text-align: right;
	padding-bottom: 24px;
}

.rmc-demo-button {
	background-color: #002896;
	border: none;
	padding: 8px;
	border-radius: 4px;
	color: #ffff;
	font-size: 14px;
	font-weight: 400;
	line-height: 20px;
}
